<template>
    <div class="main-content">
        

        <el-container>
          <el-aside :width="asideWidth" style="overflow: hidden;">
              
              <el-switch
                v-model="isCollapse"
                active-color="#13ce66"
                inactive-color="#ff4949"
                  active-text="开"
                  inactive-text="关"
                  >
              </el-switch>

              <el-menu class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="!isCollapse">
                <el-submenu :index="item.campaign_id" v-for="(item,index) in CampaignsList" :key="item.campaign_id">
                  <template slot="title" >
                    {{item.title}}
                    <el-badge :value="typeof item.adgroup == 'object' ? ''+item.adgroup.length+'':''" class="item" >
                    <span slot="title">{{item.title}}</span>
                    </el-badge>
                  </template>
                  <el-menu-item-group v-if="typeof item.adgroup !='undefined' ">
                    <el-menu-item 
                    v-for="adgroup in item.adgroup"
                    @click="filterData({type:'and',items:[{sItem:'adgroup_id',sOperator:'==',sValue:adgroup.adgroup_id}]})"
                    :index="item.campaign_id+'-'+adgroup.adgroup_id"
                    >{{adgroup.title}}</el-menu-item>
                  </el-menu-item-group>
                </el-submenu>

              </el-menu>
              
              
          </el-aside>
          <el-main>
              
              
              <div class="top-option">
                  
                  <div style="text-align: right;">
                      
                      <div style="display: inline; background-color: #f0f9eb;color: #67c23a;padding: 5px;margin:0 20px" v-if="chooseInfo">
                            {{chooseInfo}}<i @click="resetFilter" class="el-icon-circle-close" style="font-size: 20px;margin-left: 10px;cursor: pointer;color:#FF5500"></i>
                            
                            
                          </div>
                      <div style="display: inline;">
                          <el-button icon="el-icon-set-up" @click="dialogChooseFormVisible=true">筛选</el-button>
                      </div>
                      
                      <div style="margin-left: 20px;display: inline">
                          选择数据周期：
                          <el-select v-model="duringTime" @change="resetDuringTime">
                              <el-option label="昨天" value="last1day"></el-option>
                              <el-option label="最近7天"  value="last7days"></el-option>
                              <el-option label="最近14天" value="last14days"></el-option>
                              <el-option label="最近30天" value="last30days"></el-option>
                              </el-select>
                      </div>
                      
                  </div>
                  
                  
                  
                  <el-dialog title="筛选选项" :visible.sync="dialogChooseFormVisible">
                      <el-form :model="chooseform">
                          <el-form-item>
                              <el-select v-model="choosetype">
                                <el-option label="且" value="and" ></el-option>
                                <el-option label="或" value="or" ></el-option>
                                </el-select>
                              <i class="el-icon-circle-plus chooseform-icon" @click="addChooseItem"  ></i>
                              
                              <span style="margin-left: 20px;">请点图标添加筛选条件</span>
                          </el-form-item>
                          
                            
                            

                          <el-form-item label="" :label-width="formLabelWidth" v-for="(chooseform, index) in chooseformArr">
                            <el-select v-model="chooseform.sItem" >
                            <el-option :label="c.title" v-for="c in columns" :value="c.field" v-if="c.title"></el-option>
                            </el-select> 
                            
                            <el-select v-model="chooseform.sOperator" style="margin-left: 10px;">
                            <el-option :label="m.title" v-for="m in methods" :value="m.method"></el-option>
                            </el-select> 
                            <el-input v-model="chooseform.sValue" style="margin-left: 10px;"></el-input>
                            </el-radio> 
                            
                            <i class="el-icon-circle-close chooseform-icon" @click="chooseformArr.splice(index,1)" style="font-size: 20px; cursor: pointer;"></i>
                          </el-form-item>
                          
                                                    
                          
                        </el-form>
                  
                    <div slot="footer" class="dialog-footer">
                      <el-button @click="dialogChooseFormVisible = false">取 消</el-button>
                      <el-button type="primary" @click="submitChooseForm" >确 定</el-button>
                    </div>
                  </el-dialog>
                  
                  
                  
                  
              </div>
              
              <el-dialog title="修改人群溢价" :visible.sync="dialogFormVisible">
                  <el-form :model="form">
                      <el-form-item label="固定溢价" :label-width="formLabelWidth">
                        <el-radio v-model="form.radio" label="1">  <el-input v-model="form.value1" :disabled="form.radio!='1'"></el-input></el-radio>  
                      </el-form-item>
                      <el-form-item label="比例溢价" :label-width="formLabelWidth">
                        <el-radio v-model="form.radio" label="2">  
                        <el-select v-model="form.method2" :disabled="form.radio!='2'" >
                        <el-option label="增加" value="plus"></el-option>
                        <el-option label="减少" value="decrease"></el-option>
                        </el-select> 
                        <el-input v-model="form.rate2" :disabled="form.radio!='2'" style="margin-left: 10px;"></el-input>%  溢价不高于：<el-input v-model="form.limit2" :disabled="form.radio!='2'" style="margin-left: 10px;"></el-input></el-radio> 
                      </el-form-item>
                      
                      <el-form-item label="增减溢价" :label-width="formLabelWidth">
                        <el-radio v-model="form.radio" label="3">  
                        <el-select v-model="form.method3" :disabled="form.radio!='3'">
                          <el-option label="增加" value="plus"></el-option>
                          <el-option label="减少" value="decrease"></el-option>
                        </el-select>
                        <el-input v-model="form.rate3" :disabled="form.radio!='3'" style="margin-left: 10px;"></el-input> &nbsp;&nbsp;  溢价不高于：<el-input v-model="form.limit3" :disabled="form.radio!='3'" style="margin-left: 10px;"></el-input></el-radio>
                      </el-form-item>
                      
                      
                    </el-form>
              
                <div slot="footer" class="dialog-footer">
                  <el-button @click="dialogFormVisible = false">取 消</el-button>
                  <el-button type="primary" id="change-discount-submit" >确 定</el-button>
                </div>
              </el-dialog>
              
                  
                  
                  
                  <div class="toolbar form-inline">
                    <el-button id="btn-online" icon="el-icon-video-play" data-action="online">开始推广</el-button>              
                    <el-button id="btn-offline" icon="el-icon-video-pause" data-action="offline">停止推广</el-button>              
                    <el-button id="btn-delete" icon="el-icon-delete" data-action="delete">删除</el-button>              
                    <el-button id="btn-discount" icon="el-icon-edit" data-action="discount" >修改溢价</el-button>
                    
                  </div>
                  
                  
                  
                  <div  v-loading="loading" :element-loading-text="loadingText" class="table-div"> 
                  <table id="table"  style="width:2500px;"></table>
                  
                  <div>汇总数据：</div>
                  <div>总展现：{{totalImpression}} 总点击：{{totalClick}} 总花费：{{(totalCost/100).toFixed(2)}}元 ,点击单价{{(totalCost/100/totalClick).toFixed(2)}}元，成交金额{{(totalTransactiontotal/100).toFixed(2)}}元，成交数量{{totalTransactionshippingtotal}}，购物车数{{totalCarttotal}}，总收藏数{{totalFavtotal}}，</div>
                  </div>
              
          </el-main>
        </el-container>
        


            
  <!-- <BootstrapTable :columns="columns" :data="data" :options="options"></BootstrapTable> -->

    </div>
     
</template>

<script>
    

      
      
    import {request} from "@/network/request.js";
    import Qs from 'qs'
    
    export default {
        name: 'auto',
        data() {
            return {
    
                columns: [
                  {
                    checkbox:true
                  },
                  {
                    title: '人群ID',
                    field: 'crowd_crowd_id',
                    sortable:true,
                  },
                  {
                    field: 'campaign_title',
                    title: '计划',
                    sortable:true,
                    width:"200px",
                  },
                  {
                    field: 'crowd_name',
                    title: '人群',
                    sortable:true,
                    width:"240px",
                  }, 
                  {
                    field: 'discount',
                    title: '溢价',
                    sortable:true,
                    width:"80px",
                  },
                  {
                    field: 'online_status_text',
                    title: '状态',
                    sortable:true,
                    width:"80px",
                  },
                  {
                    field: 'crowd_type',
                    title: '人群类型',
                    sortable:true,
                    width:"100px",
                  },
                  
                  {
                    field: 'num_iid_title',
                    title: '宝贝',
                    sortable:true,
                    width:"250px",
                    class:"table-item-title"
                  },
                  {
                    field: 'num_iid',
                    title: '宝贝num_iid',
                    sortable:true,
                  },
                  {
                    field: 'click',
                    title: '点击',
                    sortable:true,
                  },
                  {
                    field: 'cost',
                    title: '花费',
                    sortable:true,
                  },
                  {
                    field: 'cpc',
                    title: '单次点击费用',
                    sortable:true,
                  },
                  {
                    field: 'cpm',
                    title: '单次展示费用',
                    sortable:true,
                  },
                  {
                    field: 'roi',
                    title: 'ROI',
                    sortable:true,
                  },
                  {
                    field: 'ctr',
                    title: '点击率',
                    sortable:true,
                  },
                  {
                    field: 'favototal',
                    title: '收藏总数',
                    sortable:true,
                  },
                  {
                    field: 'carttotal',
                    title: '加购物车',
                    sortable:true,
                  },
                  {
                    field: 'impression',
                    title: '展示',
                    sortable:true,
                  },
                  {
                    field: 'transactionshippingtotal',
                    title: '成交数量',
                    sortable:true,
                  },
                  {
                    field: 'transactiontotal',
                    title: '成交金额',
                    sortable:true,
                  },
                  
                  
                ],
                methods:[
                    {
                        method:'==',
                        title:'等于',
                    },
                    {
                        method:'<>',
                        title:'不等于',
                    },
                    {
                        method:'>',
                        title:'大于',
                    },
                    {
                        method:'>=',
                        title:'大于等于',
                    },
                    {
                        method:'<',
                        title:'小于',
                    },
                    {
                        method:'<=',
                        title:'小于等于',
                    },
                    {
                        method:'in',
                        title:'包含',
                    },
                    {
                        method:'notIn',
                        title:'不包含',
                    },
                ],
                data: [],  //原始数据
                TableData:[],//呈现到表格的数据
                loadDataTimes:0,
                options: {
                  search: true,
                  showColumns: true,
                  sortable:true,
                  sorting:true,
                  singleSelect:true,
                },
                dialogFormVisible:false, //修改人群溢价对话框
                form:{   //修改溢价的表单
                    radio:"1",
                    value1:"",
                    
                    method2:'plus',
                    rate2:'',
                    limit2:'300',
                    method3:'plus',
                    rate3:'',
                    limit3:'300',
                },
                formLabelWidth:'200',
                duringTime:"last1day", //报表显示周期,默认显示昨天
                loading:false,
                loadingText:'加载中',
                isCollapse: true,
                CampaignsList:[], //推广计划列表
                totalClick:0,//总点击次数
                totalCost:0,//总花费
                totalImpression:0,
                totalCarttotal:0,
                totalTransactionshippingtotal:0,
                totalTransactiontotal:0,
                totalFavtotal:0,
                asideWidth:"300px",//左边宽度
                dialogChooseFormVisible:false, //筛选选项弹出框
                chooseformArr:[{sItem:'impression',sOperator:'>=',sValue:'200'}], //筛选框
                chooseform:{},
                choosetype:"and" ,//筛选关系 and ,or 
                
                filter:{}, //筛选条件,方便清除最近的条件
                

            }
        },
        components:{
        },
        watch:{
            isCollapse(val){
                this.asideWidth = val? "300px": "100px";
            }
        },
        computed:{
            chooseInfo: function()
            {
                let str;
                
                if(typeof this.filter.items != 'undefined')
                str = "当前筛选条件："+this.filter.items[0].sItem+this.filter.items[0].sOperator+this.filter.items[0].sValue;
                
                return str;
                
                
            }
        },
        methods:{
                  handleOpen(key, keyPath) {
                    console.log(key, keyPath);
                    this.filterData({type:"and",items:[{sItem:'campaign_id',sOperator:'==',sValue:key}]})
                    
                  },
                  handleClose(key, keyPath) {
                    console.log(key, keyPath);
                  },
                  
                  addChooseItem()
                  {
                      this.chooseformArr.push([]);
                  },
                  submitChooseForm()
                  {
                      console.log({type:this.choosetype,items:this.chooseformArr})
                      this.filterData({type:this.choosetype,items:this.chooseformArr});
                  },
                  
            resetDuringTime()
            {
                var _this = this;
                
                this.loading=true;
                
                request({
                    url: "/crowd/getCrowdByDuringTime",
                    params:{
                        duringTime:this.duringTime,
                    },
                }).then(res => {
                    console.log('getCrowdByDuringTime:',res)

                    this.data=res.data;
                    
                    
                    
                    if(this.loadDataTimes <1)
                    {
                        this.loadDataTimes ++;
                                       
                        if(this.data.length ==0 )
                        {
                            this.$message('需要获取更新数据，请耐心等待……');
                        }
                        
                        this.SyncCamppaignAdgroupCrowd();
                    } else {
                        this.loading = false;
                    }
                    
                    
                    this.resetTotalData(res.data);
                    
                    

                    $('#table').bootstrapTable('destroy').bootstrapTable({
                      
                      method: 'GET',
                      uniqueId: 'id',                        // 绑定ID，不显示
                      striped: false,                         //是否显示行间隔色
                      cache: false,                          //是否使用缓存，默认为true，所以一般情况下需要设置一下这个属性（*）
                      sortable: true,                        //是否启用排序
                      sortOrder: "asc",                      //排序方式
                      sidePagination: "client",              //分页方式：client客户端分页，server服务端分页（*）
                      undefinedText: '--',
                      //singleSelect: true,                  // 单选checkbox，默认为复选
                      // showRefresh   : true,                  // 显示刷新按钮
                      showColumns   : true,                  // 选择显示的列
                      toolbar: '#item_info_toolbar',         // 搜索框位置
                      search: true,                          // 搜索开启,
                      strictSearch: true,
                      clickToSelect: true,                   // 点击选中行
                      pagination: true,                      //是否显示分页
                      pageNumber:1,                          //初始化加载第一页，默认第一页,并记录
                      pageSize:20,//每页显示的数量
                      pageList: [5, 10, 20, 50, 100],//设置每页显示的数量
                      paginationPreText:"上一页",
                      paginationNextText:"下一页",
                      paginationLoop:false,
                      //showToggle: true,                   //是否显示详细视图和列表视图的切换按钮
                      //cardView: false,                    //是否显示详细视图
                      //detailView: false,                  //是否显示父子表
                      //showPaginationSwitch: true,
                      //得到查询的参数
                      queryParams : function (params) {
                        //这里的键的名字和控制器的变量名必须一直，这边改动，控制器也需要改成一样的
                        var temp = {
                          rows: params.limit,                         //页面大小
                          page: (params.offset / params.limit) + 1,   //页码
                          sort: params.sort,      //排序列名
                          sortOrder: params.order //排位命令（desc，asc）
                        };
                        return temp;
                      },
                      columns: _this.columns,
                      data:res.data,
                    toolbar: '.toolbar',
                    fixedColumns: true,
                    fixedNumber: 1,
                      onLoadSuccess: function () {
                        alert('表格加载成功！');
                      },
                      onLoadError: function () {
                        showTips("数据加载失败！");
                      },
                      onDblClickRow: function (row, $element) {
                        var id = row.ID;
                        //EditViewById(id, 'view');
                      },
                      rowStyle: function (row, index) { //设置行的特殊样式
                        //这里有5个取值颜色['active', 'success', 'info', 'warning', 'danger'];
                        var strclass = "";
                        if (index == 1) {
                          strclass = "warning";
                          console.log(row);
                        }
                        return { classes: strclass }
                      }
                    });
                    
                    
                })
            },
            
            SyncCamppaignAdgroupCrowd()
            {
                request({
                    url: "/crowdCommand/SyncCamppaignAdgroupCrowd"
                }).then(res => {
                    console.log('SyncCamppaignAdgroupCrowd:',res)      
                    
                    
                    //如果未获取到数据，则更新数据
                    
                    console.log(this.data.length);
                    
                    if(this.data.length ==0 )
                    {
                        console.log('reget data')
                        this.resetDuringTime();
                    } else {
                        this.loading=false;
                    }

                    
                });
                
            },
            
            resetFilter()
            {
                this.filter={};
                
                $('#table').bootstrapTable('load', this.data);
            },
            filterData(filter)
            {
                let newData = [];
                
                // let filter = [['num_iid','==','615025717319'],['click','>','0']]
                
                let str =[];
                
                this.filter = filter;
                
                
                filter.items.forEach((f)=>{
                    str.push("(item."+f.sItem+" "+ f.sOperator +" '" + f.sValue+"')") ;
                })
                
                let joinType;
                if(filter.type == "and")
                {
                    console.log(str.join('&&'));
                    joinType = '&&';
                } else {
                    console.log(str.join('||'));
                    joinType = '||';
                }
                
                
                this.data.forEach((item)=>{
                    if (eval(str.join(joinType)) )
                    {
                        newData.push(item);
                    }
                })

                
                this.TableData = newData;
                
                this.resetTotalData(newData);
                
                $('#table').bootstrapTable('load', newData);
                
            },
            resetTotalData(data)
            {
                //生成综合统计
                let totalClick=0;
                let totalCost=0;
                let totalImpression=0;
                let totalCarttotal=0;
                let totalTransactionshippingtotal=0;
                let totalTransactiontotal=0;
                let totalFavtotal=0;
                

                for(let i=0; i<data.length;i++)
                {
                    if(typeof data[i]['click'] != 'undefined')
                    totalClick += parseInt(data[i]['click']);
                    
                    if(typeof data[i]['cost'] != 'undefined')
                    totalCost += parseInt(data[i]['cost']);
                    
                    
                    if(typeof data[i]['impression'] != 'undefined')
                    totalImpression += parseInt(data[i]['impression']);
                    
                    if(typeof data[i]['carttotal'] != 'undefined')
                    totalCarttotal += parseInt(data[i]['carttotal']);
                    
                    if(typeof data[i]['transactionshippingtotal'] != 'undefined')
                    totalTransactionshippingtotal += parseInt(data[i]['transactionshippingtotal']);
                    
                    if(typeof data[i]['transactiontotal'] != 'undefined')
                    {
                    totalTransactiontotal += parseInt(data[i]['transactiontotal']);
                    console.log(totalTransactiontotal);
                    }
                    
                    if(typeof data[i]['favtotal'] != 'undefined')
                    totalFavtotal += parseInt(data[i]['favtotal']);
                
                    
                }
                
                this.totalClick = totalClick;
                this.totalCost = totalCost;
                this.totalImpression = totalImpression;
                this.totalCarttotal = totalCarttotal;
                this.totalTransactionshippingtotal = totalTransactionshippingtotal;
                this.totalTransactiontotal = totalTransactiontotal;
                this.totalFavtotal = totalFavtotal;
            }
            

            
        },
        mounted()
        {
            var _this = this;
            
            this.resetDuringTime();
            
            
            //获取推广计划
            request({
                url: "/crowd/CampaignsGet",
            }).then(res => {
                console.log("CampaignsGet:", res)
                this.CampaignsList = res.data;
                
            })

             var $table = $('#table')
              var $button = $('#button')
                  
             $button.click(function () {
               alert('getSelections: ' + JSON.stringify($table.bootstrapTable('getSelections')))
             })
             
             $('#btn-online').click(function(){
                 updateCrowd("state","online");
                 
             })
             
             $('#btn-offline').click(function(){
                 updateCrowd("state","offline");
             })
             
             
             $('#btn-delete').click(function(){
                 updateCrowd("delete","");
             })
             
             
             $('#btn-discount').click(function(){
                 _this.dialogFormVisible = true;
             })
             
             
             $('#change-discount-submit').click(function(){
                 
                 updateCrowd("discount",_this.form);
                 
             })


            function updateCrowd(action,value)
            {
                var selected = $table.bootstrapTable('getSelections');
                

                if(selected.length==0)
                {
                    _this.$message.error('请先勾选要操作的列表项');
                    return;
                }
                

                _this.$confirm('确定要执行批量操作？(影响'+selected.length+'项)', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                    request({
                        url: "/crowd/updateCrowd",
                        data:Qs.stringify({selected, action, value}),
                        method:"post"
                    }).then(res => {
                        console.log('status:',res)
                    
                    })
                });
                
            }
 

        }
        
    
    
    }
</script>

<style>
.main-content {
    margin-top:10px;
    width:100%;
    }
.table-item-title {
    overflow: hidden;
    height:12px;
    color:#005CBF
}
.el-input {
    width:100px;
}


#table {table-layout:fixed;} 
th, td{word-break:break-all;}  .fixed-table-container thead th .th-inner{ white-space:normal; }


.el-menu-item, .el-menu-item-group__title {
    padding-left: -30px;
}
.el-submenu__title {
    overflow: hidden;
    }
.el-aside {
    padding-top: 20px;
}

.chooseform-icon {
    font-size: 20px;cursor: pointer;
    margin-left: 20px;
}
</style>
